
.session-content {
  margin: 30px 30px 0 130px;
}
.master-card-header {
  background: #FFFFFF;
  border-radius: 5px 5px 0 0;
}
.master-card-picture {
  border-radius: 5px 0 0 0;
}
.master-card-footer .cell select,
.master-card-footer .cell input,
.master-card-footer .cell button {
  margin: 0;
}

.main-menu .main-menu-icon {
  max-width: 40px;
  max-height: 40px;
}
@media (max-width: 40em), (orientation: portrait) {
.session-content {
    margin: 30px 30px 100px;
}
.main-menu {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: auto;
    text-align: center;
    z-index: 10;
    margin: 0;
    padding: 0;
    border: none;
    top: unset;
}
.main-menu ul li {
    padding: 10px;
    height: auto;
    display: inline-block;
    width: auto;
    margin: 0;
    font-size: 14px;
    border: none;
}
.main-menu .main-menu-icon {
    max-width: 20px;
    max-height: 20px;
}
.main-menu.submenu-shown {
    z-index: 100;
    width: 100%;
}
.main-menu ul.submenu {
    position: static;
    width: 100%;
    height: 100%;
}
.main-menu ul.submenu li {
    border-bottom: 1px solid #C4BEB6;
    height: auto;
    width: auto;
    margin: 0;
    padding: 0;
    text-align: left;
    display: block;
}
.main-menu ul.submenu li a {
    display: block;
    padding: 10px 14px;
}
}
